import { useState } from 'react'
import { Link, useLocation, useNavigate } from 'react-router-dom'
import {
  Users,
  Shield,
  Key,
  LogOut,
  Menu,
  X,
  LayoutDashboard,
  Settings,
  Lock,
  UserCircle
} from 'lucide-react'
import { Button } from '@/components/ui/button'
import { cn } from '@/lib/utils'
import { authService } from '@/lib/auth'

const navigation = [
  {
    name: 'Panel de Control',
    href: '/dashboard',
    icon: LayoutDashboard,
    adminOnly: false,
    requiresApprovedUser: true  // Solo usuarios con rol aprobado (no new_user)
  },
  { name: 'Usuarios', href: '/admin/users', icon: Users, adminOnly: true },
  { name: 'Roles', href: '/admin/roles', icon: Shield, adminOnly: true },
  { name: 'Claves API', href: '/admin/api-keys', icon: Key, adminOnly: true },
  { name: 'Configuraci칩n', href: '/admin/settings', icon: Settings, adminOnly: true },
]

const bottomNavigation = [
  { name: 'Mi Perfil', href: '/profile', icon: UserCircle },
  { name: 'Cambiar Contrase침a', href: '/change-password', icon: Lock },
]

export function AdminLayout({ children }: { children: React.ReactNode }) {
  const [sidebarOpen, setSidebarOpen] = useState(false)
  const location = useLocation()
  const navigate = useNavigate()

  // Get user and check permissions
  const user = authService.getUser()
  const isAdmin = user?.role === 'admin' || user?.role === 'root'
  const isApprovedUser = user?.role && !['new_user'].includes(user.role.toLowerCase())
  const mustChangePassword = user?.must_change_password === true

  // Filter navigation based on user role
  const visibleNavigation = navigation.filter(item => {
    // Admin-only items: only for admin/root
    if (item.adminOnly && !isAdmin) return false
    // Items requiring approved user: not for new_user
    if (item.requiresApprovedUser && !isApprovedUser) return false
    return true
  })

  // If user must change password, don't show sidebar
  if (mustChangePassword) {
    return <div className="min-h-screen bg-gray-50">{children}</div>
  }

  const handleLogout = async () => {
    try {
      await fetch('/api/auth/logout', {
        method: 'POST',
        credentials: 'include'
      })
    } catch (error) {
      console.error('Logout failed:', error)
    } finally {
      // Clear local auth data regardless of API call result
      authService.clearAuth()
      navigate('/login')
    }
  }

  return (
    <div className="min-h-screen bg-gray-50">
      {/* Mobile sidebar overlay */}
      <div className={cn(
        "sidebar-mobile fixed inset-0 z-40",
        sidebarOpen ? "block" : "hidden"
      )}>
        <div className="fixed inset-0 bg-black/60 backdrop-blur-sm" onClick={() => setSidebarOpen(false)} />
        <div className="fixed inset-y-0 left-0 flex w-72 flex-col bg-gradient-to-b from-slate-900 via-slate-800 to-slate-900 shadow-2xl">
          {/* Mobile Header */}
          <div className="flex h-20 items-center justify-between px-6 border-b border-slate-700/50 bg-gradient-to-r from-blue-600/20 to-purple-600/20">
            <div className="flex items-center gap-3">
              <div className="p-2 bg-gradient-to-br from-blue-500 to-purple-600 rounded-lg shadow-lg">
                <LayoutDashboard className="h-6 w-6 text-white" />
              </div>
              <span className="text-xl font-bold bg-gradient-to-r from-blue-400 to-purple-400 bg-clip-text text-transparent">
                BiblioServer
              </span>
            </div>
            <Button
              variant="ghost"
              size="icon"
              onClick={() => setSidebarOpen(false)}
              className="text-slate-300 hover:text-white hover:bg-slate-800 rounded-lg"
            >
              <X className="h-6 w-6" />
            </Button>
          </div>

          {/* Mobile Navigation */}
          <nav className="flex-1 space-y-2 px-4 py-6 overflow-y-auto">
            {visibleNavigation.map((item) => {
              const isActive = location.pathname === item.href
              return (
                <Link
                  key={item.name}
                  to={item.href}
                  className={cn(
                    "group flex items-center gap-4 rounded-xl px-4 py-3.5 text-sm font-semibold transition-all duration-200",
                    isActive
                      ? "bg-gradient-to-r from-blue-600 to-purple-600 text-white shadow-lg shadow-blue-500/30"
                      : "text-slate-300 hover:bg-slate-800/60 hover:text-white"
                  )}
                  onClick={() => setSidebarOpen(false)}
                >
                  <item.icon className={cn(
                    "h-5 w-5 flex-shrink-0 transition-transform duration-200",
                    isActive ? "text-white" : "text-slate-400 group-hover:text-blue-400"
                  )} />
                  <span className="truncate">{item.name}</span>
                  {isActive && (
                    <div className="ml-auto h-2 w-2 rounded-full bg-white animate-pulse" />
                  )}
                </Link>
              )
            })}
          </nav>

          {/* Mobile Footer */}
          <div className="border-t border-slate-700/50 bg-slate-900/50 backdrop-blur-sm p-4 space-y-2">
            <div className="px-3 py-2 mb-3">
              <div className="flex items-center gap-3">
                <div className="p-2 bg-gradient-to-br from-emerald-500 to-blue-500 rounded-full">
                  <UserCircle className="h-5 w-5 text-white" />
                </div>
                <div className="flex-1 min-w-0">
                  <p className="text-sm font-semibold text-white truncate">
                    {authService.getUser()?.username || 'Admin'}
                  </p>
                  <p className="text-xs text-slate-400 truncate">
                    {authService.getUser()?.role || 'Administrator'}
                  </p>
                </div>
              </div>
            </div>

            <Link
              to="/profile"
              className={cn(
                "group flex items-center gap-4 rounded-xl px-4 py-2.5 text-sm font-medium transition-all duration-200",
                location.pathname === '/profile'
                  ? "bg-slate-800 text-blue-400 shadow-md"
                  : "text-slate-400 hover:bg-slate-800/60 hover:text-white"
              )}
              onClick={() => setSidebarOpen(false)}
            >
              <UserCircle className="h-4 w-4 flex-shrink-0" />
              <span className="truncate">Mi Perfil</span>
            </Link>

            <Link
              to="/change-password"
              className={cn(
                "group flex items-center gap-4 rounded-xl px-4 py-2.5 text-sm font-medium transition-all duration-200",
                location.pathname === '/change-password'
                  ? "bg-slate-800 text-blue-400 shadow-md"
                  : "text-slate-400 hover:bg-slate-800/60 hover:text-white"
              )}
              onClick={() => setSidebarOpen(false)}
            >
              <Lock className="h-4 w-4 flex-shrink-0" />
              <span className="truncate">Cambiar Contrase침a</span>
            </Link>

            <Button
              variant="ghost"
              className="w-full justify-start gap-4 text-sm font-medium text-red-400 hover:text-red-300 hover:bg-red-950/30 rounded-xl py-2.5 px-4 transition-all duration-200"
              onClick={handleLogout}
            >
              <LogOut className="h-4 w-4" />
              <span>Logout</span>
            </Button>
          </div>
        </div>
      </div>

      {/* Desktop sidebar - always visible on tablets and up */}
      <div className="sidebar-desktop">
        <div className="flex flex-col h-screen bg-gradient-to-b from-slate-900 via-slate-800 to-slate-900 shadow-2xl">
          {/* Logo Header with modern design */}
          <div className="flex h-20 items-center justify-center px-6 border-b border-slate-700/50 bg-gradient-to-r from-blue-600/20 to-purple-600/20">
            <div className="flex items-center gap-3">
              <div className="p-2 bg-gradient-to-br from-blue-500 to-purple-600 rounded-lg shadow-lg">
                <LayoutDashboard className="h-6 w-6 text-white" />
              </div>
              <span className="text-xl font-bold bg-gradient-to-r from-blue-400 to-purple-400 bg-clip-text text-transparent">
                BiblioServer
              </span>
            </div>
          </div>

          {/* Navigation with improved spacing and hover effects */}
          <nav className="flex-1 space-y-2 px-4 py-6 overflow-y-auto">
            {visibleNavigation.map((item) => {
              const isActive = location.pathname === item.href
              return (
                <Link
                  key={item.name}
                  to={item.href}
                  className={cn(
                    "group flex items-center gap-4 rounded-xl px-4 py-3.5 text-sm font-semibold transition-all duration-200",
                    isActive
                      ? "bg-gradient-to-r from-blue-600 to-purple-600 text-white shadow-lg shadow-blue-500/30 scale-105"
                      : "text-slate-300 hover:bg-slate-800/60 hover:text-white hover:scale-105 hover:shadow-md"
                  )}
                >
                  <item.icon className={cn(
                    "h-5 w-5 flex-shrink-0 transition-transform duration-200",
                    isActive ? "text-white" : "text-slate-400 group-hover:text-blue-400 group-hover:scale-110"
                  )} />
                  <span className="truncate">{item.name}</span>
                  {isActive && (
                    <div className="ml-auto h-2 w-2 rounded-full bg-white animate-pulse" />
                  )}
                </Link>
              )
            })}
          </nav>

          {/* Footer section with user actions */}
          <div className="border-t border-slate-700/50 bg-slate-900/50 backdrop-blur-sm p-4 space-y-2">
            <div className="px-3 py-2 mb-3">
              <div className="flex items-center gap-3">
                <div className="p-2 bg-gradient-to-br from-emerald-500 to-blue-500 rounded-full">
                  <UserCircle className="h-5 w-5 text-white" />
                </div>
                <div className="flex-1 min-w-0">
                  <p className="text-sm font-semibold text-white truncate">
                    {authService.getUser()?.username || 'Admin'}
                  </p>
                  <p className="text-xs text-slate-400 truncate">
                    {authService.getUser()?.role || 'Administrator'}
                  </p>
                </div>
              </div>
            </div>

            <Link
              to="/profile"
              className={cn(
                "group flex items-center gap-4 rounded-xl px-4 py-2.5 text-sm font-medium transition-all duration-200",
                location.pathname === '/profile'
                  ? "bg-slate-800 text-blue-400 shadow-md"
                  : "text-slate-400 hover:bg-slate-800/60 hover:text-white"
              )}
            >
              <UserCircle className="h-4 w-4 flex-shrink-0 group-hover:scale-110 transition-transform" />
              <span className="truncate">Mi Perfil</span>
            </Link>

            <Link
              to="/change-password"
              className={cn(
                "group flex items-center gap-4 rounded-xl px-4 py-2.5 text-sm font-medium transition-all duration-200",
                location.pathname === '/change-password'
                  ? "bg-slate-800 text-blue-400 shadow-md"
                  : "text-slate-400 hover:bg-slate-800/60 hover:text-white"
              )}
            >
              <Lock className="h-4 w-4 flex-shrink-0 group-hover:scale-110 transition-transform" />
              <span className="truncate">Cambiar Contrase침a</span>
            </Link>

            <Button
              variant="ghost"
              className="w-full justify-start gap-4 text-sm font-medium text-red-400 hover:text-red-300 hover:bg-red-950/30 rounded-xl py-2.5 px-4 transition-all duration-200 group"
              onClick={handleLogout}
            >
              <LogOut className="h-4 w-4 group-hover:scale-110 transition-transform" />
              <span>Logout</span>
            </Button>
          </div>
        </div>
      </div>

      {/* Main content */}
      <div className="main-content">
        <header className="sticky top-0 z-10 bg-white/80 backdrop-blur-md border-b border-gray-200 shadow-sm">
          <div className="flex h-16 items-center gap-4 px-4 lg:px-6">
            <Button
              variant="ghost"
              size="icon"
              className="menu-button hover:bg-gray-100 rounded-lg transition-colors"
              onClick={() => setSidebarOpen(true)}
            >
              <Menu className="h-6 w-6 text-gray-700" />
            </Button>
            <div className="flex-1" />

            {/* User info in header */}
            <div className="hidden sm:flex items-center gap-3 px-3 py-2 bg-gradient-to-r from-blue-50 to-purple-50 rounded-xl border border-blue-100">
              <div className="p-1.5 bg-gradient-to-br from-blue-500 to-purple-600 rounded-full">
                <UserCircle className="h-4 w-4 text-white" />
              </div>
              <div className="text-sm">
                <p className="font-semibold text-gray-900">
                  {authService.getUser()?.username || 'Admin'}
                </p>
              </div>
            </div>
          </div>
        </header>
        <main className="p-4 sm:p-6 lg:p-8 bg-gradient-to-br from-gray-50 via-blue-50/30 to-purple-50/30 min-h-screen">
          {children}
        </main>
      </div>
    </div>
  )
}
